rlay{display:none;background-color:rgba(0,0,0,0.6);width:100vw;height:100vh;top:0;left:0;position:fixed;z-index:9999}.overlay .playAgain{border-radius:5px 5px 5px 5px;margin:0 auto;background-color:#2c2c2c;padding:4%;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.overlay .playAgain button{margin:0 2% 0 2%}.overlay .playAgain button:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.show{display:block}html,body,.app{height:100vh;width:100vw;margin:0}.app{background:radial-gradient(circle at center, #3437e6, black);font-family:"Orbitron",sans-serif;box-sizing:border-box;text-align:center;color:white}div{margin:0}h1{font-family:"Black Ops One",sans-serif;font-size:9.2vw;text-transform:uppercase;margin:0;background:-webkit-linear-gradient(#e1ff5c, #c21212);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-text-stroke:0.01em black}h2{letter-spacing:0.1em;font-size:20px;margin:0;text-transform:uppercase;margin-bottom:1vw}@media (min-width: 426px) and (max-width: 1024px){h2{font-size:14px}}@media (max-width: 425px){h2{font-size:10px}}p{margin:0;font-size:16px}@media (max-width: 1024px){p{font-size:10px}}button{height:-webkit-min-content;height:min-content;text-transform:uppercase;font-family:"Orbitron",sans-serif;font-size:16px;padding:2%;color:white;background-color:#2c2c2c;border-top:.3rem solid #464646;border-bottom:.3rem solid #000;border-left:.3rem solid #131313;border-right:.3rem solid #131313;outline:none}button:hover{cursor:pointer;color:orange}

